.footer {
  width: 100%;
  padding: 2rem 0;
  background-color: #333;
  color: #fff;
}

.footer h3 {
  /* color: var(--primary-ligh); Не підтягує змінну. Знайти причину! */
  color: #709dff;
}

.footer .top, .bottom {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.footer .social {
  display: flex;
  justify-content: space-between;
  width: 200px;
}

.footer .icon {
  /* color: var(--primary-ligh); Не підтягує змінну. Знайти причину! */
  color: #709dff;
}

/* .footer .left, .right {
  display: flex;
  align-items: center;
} */

.footer ul {
  display: flex;
}

.footer li {
  padding: 0.5rem;
}

@media screen and (max-width: 1140px) {
  .footer ul {
    flex-direction: column;
  }
}